'use strict';
/*
jshint strict : true,
       esversion : 6,
       -W097,
       -W031
 */
import Vue from 'vue';
import VueResource from 'vue-resource';
import App from './components/App.vue';

Vue.use(VueResource);

new Vue({
    el: '#app',
    render(createElement) {
        // Request from the API if the client's company
        this.$http.get('/api/company').then((response) => {
            // If success, then the user must be logged in and the application can then load
            console.log(response);
            return createElement(App);
        }, (response) => {
            // If it failed with a 401 (Unauthorized) then load the login page
            console.log(response);
            // On any other error, load the 500 page (whoops)
        });
    }
});



